.profile__wrapper{padding-top:40px;display:-webkit-box;display:-ms-flexbox;display:flex}@media (max-width:1023px){.profile__wrapper{display:block}}.profile__sidebar{width:25%}@media (max-width:1023px){.profile__sidebar{width:100%;margin-bottom:5px}}.profile__user-name{font-family:HeroNew-Medium,sans-serif;font-size:17px;line-height:19px;letter-spacing:-.03em;color:#1b1b1b}.profile__list{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;margin-top:27px}@media (max-width:1023px){.profile__list{-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row}}@media (max-width:767px){.profile__list{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}}.profile__link{font-family:HeroNew-Medium,sans-serif;font-size:15px;line-height:17px;text-decoration:none;color:#6b70eb;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.profile__link:hover{color:#df00a1}.profile__link:not(:first-child){margin-top:15px}@media (max-width:1023px){.profile__link:not(:first-child){margin-top:0;margin-left:25px}}@media (max-width:767px){.profile__link:not(:first-child){margin-top:15px;margin-left:0}}.profile__link--active{color:#df00a1;pointer-events:none}.profile__main{width:75%;display:grid;grid-template-columns:repeat(2,2fr);grid-gap:30px}@media (max-width:1023px){.profile__main{width:100%}}@media (max-width:767px){.profile__main{grid-template-columns:repeat(1,1fr);grid-gap:0}}.profile__item{max-width:258px;margin-bottom:30px}@media (max-width:767px){.profile__item{max-width:100%}}.profile__item *{width:100%}