.range-slider{height:3px;border:none;background:#e5e5e5}.range-slider .noUi-connect{background:#6b70ea}.range-slider .noUi-handle{width:14px;height:14px;cursor:pointer;border-radius:50%;border:none;background:#6b70ea;-webkit-box-shadow:none;box-shadow:none;right:-7px}.range-slider .noUi-handle:after,.range-slider .noUi-handle:before{display:none}.range-slider__quantity-wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-bottom:19px}.range-slider__quantity{width:100%;padding:10px 9px;font-family:HeroNew-Regular,sans-serif;font-size:13px;line-height:18px;color:#000;border:1px solid #e5e5e5;border-radius:5px;min-width:0}.range-slider__separator{display:inline-block;width:11px;height:1px;content:"";margin:0 9px;background:#000;-ms-flex-negative:0;flex-shrink:0}