.product-catalog,.product-catalog--overflow-hide{overflow:hidden}.product-catalog--default-offset{padding:65px 0 90px}.product-catalog--small-offset{padding:35px 0}.product-catalog--background-red{background:#e33151}.product-catalog--background-black{background:#1b1b1b}.product-catalog__wrapper{display:grid;grid-template-columns:repeat(4,1fr);grid-gap:30px}@media (max-width:1023px){.product-catalog__wrapper{grid-template-columns:repeat(auto-fill,minmax(260px,1fr))}}.product-catalog__tabs-list{margin-bottom:40px}.product-catalog__buttons{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-top:30px}