.section{padding:65px 0 90px;overflow:hidden}.section--offset-none{padding:0}.section--small-offset{padding:35px 0}.section--offset-top{padding-top:64px;padding-bottom:0}.section--offset-bottom{padding-top:0;padding-bottom:30px}@media (min-width:540px){.section--offset-bottom{padding-bottom:calc(30px + (6000vw - 32400px)/484)}}@media (min-width:1024px){.section--offset-bottom{padding-bottom:90px}}.section--background-red{background:#e33151}.section--background-black{background:#1b1b1b}.section__buttons{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-top:30px}.section__button:not(:last-child){margin-right:15px}