.product-order{max-width:120px;display:block}.product-order:not(:first-child){margin-left:15px}.product-order__image-wrapper{position:relative;border-radius:5px;overflow:hidden;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;-webkit-box-shadow:0 4px 4px rgba(0,0,0,0);box-shadow:0 4px 4px rgba(0,0,0,0)}.product-order:hover .product-order__image-wrapper{-webkit-box-shadow:0 4px 4px rgba(0,0,0,.2);box-shadow:0 4px 4px rgba(0,0,0,.2)}.product-order__image{display:block;width:100%;max-height:100%}.product-order__name{margin-top:11px;font-family:HeroNew-Medium,sans-serif;font-size:11px;line-height:15px;letter-spacing:-.03em;color:#1b1b1b;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.product-order:hover .product-order__name{color:#6b70eb}.product-order__price{font-family:HeroNew-SemiBold,sans-serif;font-size:15px;line-height:21px;letter-spacing:-.03em;color:#000}.product-order__quantity{margin-top:5px;font-family:HeroNew-Medium,sans-serif;font-size:11px;line-height:15px;letter-spacing:-.03em;color:#999}