.quantity{display:-webkit-box;display:-ms-flexbox;display:flex;border-radius:20px;background:#f0f0f0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:100%;min-width:100px;height:40px}.quantity__input{width:100%;height:38px;margin:0 5px;outline:0;border:0;background-color:rgba(0,0,0,0);-webkit-appearance:none;text-align:center;padding:2px 0;font-size:13px;font-family:HeroNew-Medium,sans-serif;color:#1b1b1b}.quantity__minus,.quantity__plus{position:relative;min-width:25px;height:100%;padding:5px;cursor:pointer}.quantity__minus:after,.quantity__minus:before,.quantity__plus:after,.quantity__plus:before{position:absolute;content:"";width:100%;height:100%;top:0;left:0;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;background-color:#1b1b1b}.quantity__minus:after,.quantity__plus:after{width:2px;height:12px}.quantity__minus:before,.quantity__plus:before{width:12px;height:2px}.quantity__plus{margin-right:5px}.quantity__minus{margin-left:5px}.quantity__minus:after{display:none}