.post{color:#1b1b1b}.post h3{font-family:HeroNew-Medium,sans-serif;font-size:24px;line-height:34px;font-weight:400}@media (max-width:599px){.post h3{font-size:20px;line-height:30px}}.post p{font-family:HeroNew-Regular,sans-serif;font-size:17px;line-height:24px;letter-spacing:-.03em;margin:30px 0 20px}@media (max-width:599px){.post p{font-size:14px;line-height:22px;letter-spacing:-.03em;margin:16px 0}}.post a{color:#6b70eb;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.post a:hover{color:#df00a1}.post img{display:block;max-width:100%;margin:30px 0 45px}