.testimonials{padding-top:29px;border-top:1px solid #e5e5e5}@media (max-width:767px){.testimonials{padding-top:0;border-top:0}}.testimonials__title{font-size:30px;line-height:42px;font-family:HeroNew-Medium,sans-serif;letter-spacing:-.03em;margin-bottom:30px}.testimonials__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex}@media (max-width:767px){.testimonials__wrapper{-webkit-box-orient:vertical;-webkit-box-direction:reverse;-ms-flex-direction:column-reverse;flex-direction:column-reverse;-webkit-box-align:center;-ms-flex-align:center;align-items:center}}.testimonials__main{max-width:810px}@media (max-width:767px){.testimonials__main{border-top:1px solid #e5e5e5;margin-top:40px;padding-top:30px}}.testimonials__item{padding-bottom:29px}.testimonials__item:not(:first-child){padding-top:30px;border-top:1px solid #e5e5e5}.testimonials__name{font-family:HeroNew-Medium,sans-serif;font-size:15px;line-height:1.4;letter-spacing:-.03em;color:#1b1b1b}.testimonials__info{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;margin-top:8px}.testimonials__date{font-size:13px;line-height:18px;letter-spacing:-.03em;color:#c4c4c4}.testimonials__rating{margin-left:18px}.testimonials__rating svg{fill:#ffc200}.testimonials__desc{margin-top:11px;font-size:13px;line-height:18px;letter-spacing:-.03em;color:#1b1b1b}.testimonials__feedback{min-width:280px;padding-left:30px;margin-left:auto}@media (max-width:767px){.testimonials__feedback{min-width:250px;padding-left:0;margin:0 auto}}.testimonials__feedback-title{margin:0 auto;max-width:250px;text-align:center;font-family:HeroNew-Medium,sans-serif;font-size:17px;line-height:24px;letter-spacing:-.03em}.testimonials__feedback-rating{text-align:center;margin-top:13px}.testimonials__feedback-rating svg{width:44px;height:44px;fill:#ffc200}.testimonials__button-feedback{margin-top:23px;width:100%}