.burger-menu{position:relative;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;outline:none;border:none;min-height:18px;background:none;display:-webkit-box;display:-ms-flexbox;display:flex}.burger-menu__wrapper{display:none}@media (max-width:1023px){.burger-menu__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:33.3%}}.burger-menu__item{width:32px;height:2px;background:#fff;border-radius:4px;-webkit-transition:all .15s ease-out;-o-transition:all .15s ease-out;transition:all .15s ease-out;margin-bottom:6px}.burger-menu--active .burger-menu__item,.burger-menu__item:last-child{margin-bottom:0}.burger-menu--active .burger-menu__item:nth-child(2){opacity:0;visibility:hidden;-webkit-transform:translate(16px,7px);-ms-transform:translate(16px,7px);transform:translate(16px,7px)}.burger-menu--active .burger-menu__item:first-child{-webkit-transform:rotate(45deg) translate(7px,6px);-ms-transform:rotate(45deg) translate(7px,6px);transform:rotate(45deg) translate(7px,6px)}.burger-menu--active .burger-menu__item:last-child{-webkit-transform:rotate(-45deg) translate(-4px,4px);-ms-transform:rotate(-45deg) translate(-4px,4px);transform:rotate(-45deg) translate(-4px,4px)}