.banner{max-width:810px;min-height:280px;margin:0 auto;padding:40px 20px 36px;background-position:50%!important;background-size:cover!important;border-radius:10px}.banner__content{max-width:495px;text-align:center}.banner__title{color:#e8a473;font-size:21px;line-height:29px;margin:16px 0;text-align:left}.banner__link,.banner__title{font-family:HeroNew-Medium,sans-serif;letter-spacing:-.03em}.banner__link{-webkit-transition:color .3s ease;-o-transition:color .3s ease;transition:color .3s ease;display:inline-block;margin-top:17px;color:#999;font-size:13px;line-height:18px;text-decoration:none}.banner__link:hover{color:#fff}