.pagination{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;max-width:350px;margin:0 auto}.pagination,.pagination__counters{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.pagination__counters{background:hsla(0,0%,91%,.7);border-radius:20px;padding:0 5px}.pagination__counter-current,.pagination__counter-link,.pagination__next,.pagination__prev{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;min-width:30px;height:30px;border-radius:50%;font-size:13px;font-family:HeroNew-Regular,sans-serif;text-decoration:none;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}@media (max-width:599px){.pagination__counter-current,.pagination__counter-link,.pagination__next,.pagination__prev{min-width:24px;height:24px;font-size:10px}}.pagination__next svg{-webkit-transform:scale(-1);-ms-transform:scale(-1);transform:scale(-1)}.pagination__counter-link,.pagination__next,.pagination__prev{color:#666;margin:5px}.pagination__counter-link:hover,.pagination__next:hover,.pagination__prev:hover{background:#c4c4c4}.pagination__counter-current{background:#1b1b1b;color:#fff}