.subscribe{position:relative;background:#fff;padding:60px 0 30px;max-width:480px;margin:0 auto}@media (max-width:767px){.subscribe{padding-top:125px;overflow:hidden}}.subscribe__title{position:relative;text-align:center}.subscribe__desc{font:15px/1.4 HeroNew-Medium,sans-serif;letter-spacing:-.03em;color:#1b1b1b}.subscribe__icon{position:absolute;left:-100px;bottom:0;pointer-events:none;z-index:1;-webkit-transform:scale(1.871) rotateY(180deg);transform:scale(1.871) rotateY(180deg);fill:#ffc200}@media (max-width:767px){.subscribe__icon{left:50%;margin-left:-36px;bottom:64px}}.subscribe__form{margin:20px 45px 0;display:-webkit-box;display:-ms-flexbox;display:flex}.subscribe__input{font-size:12px;font-family:HeroNew-Medium,sans-serif;letter-spacing:-.03em;height:40px;padding:10px;border-radius:5px 0 0 5px;border:1px solid #e5e5e5;outline:0;background:#fff;color:#1b1b1b}.subscribe__button{width:100%;border-radius:0 5px 5px 0;color:#999;background:#f0f0f0;padding-top:11px;padding-bottom:11px}