:root{--white-color:#fff;--black-color:#1c1c1c;--gray-light-color:#f9f9f9;--gray-middle-color:#e1ddd7;--gold-color:#f0b46c;--gold-light-color:#b0a192;--blue-dark-color:#151723}::-moz-selection{background:#ccc;color:#4f4c66}::selection{background:#ccc;color:#4f4c66}*,:after,:before{-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-tap-highlight-color:transparent}*{margin:0;padding:0;outline:none}html{overflow-x:hidden}body{font-family:HeroNew-Regular,sans-serif;font-weight:300;font-size:14px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:none;margin:0;color:var(--black-color);background:#fff;padding-top:150px}@media (max-width:1023px){body{padding-top:70px}}button{cursor:pointer}b{font-family:HeroNew-SemiBold,sans-serif;font-weight:400}a{text-decoration:none}ul{list-style:none}.h3,h3{font-family:HeroNew-Medium,sans-serif;font-size:16px}@media (min-width:768px){.h3,h3{font-size:calc(16px + (800vw - 6144px)/256)}}@media (min-width:1024px){.h3,h3{font-size:24px}}.h3,h3{line-height:22px}@media (min-width:768px){.h3,h3{line-height:calc(22px + (1200vw - 9216px)/256)}}@media (min-width:1024px){.h3,h3{line-height:34px}}