.filter-news,.filter-news__link{display:-webkit-box;display:-ms-flexbox;display:flex}.filter-news__link{-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding:5px 15px 7px;text-decoration:none;font-family:HeroNew-Medium,sans-serif;font-size:13px;line-height:18px;letter-spacing:-.03em;color:#666;background:#f0f0f0;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.filter-news__link--active{background:#000;color:#fff}.filter-news__link:hover{background:rgba(27,27,27,.5);color:#fff}.filter-news__link:first-child{border-radius:15px 0 0 15px}.filter-news__link:last-child{border-radius:0 15px 15px 0}.filter-news__link:not(:first-child){margin-left:2px}